<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Subscription Detail</p>
        </div>
        <div class="orderDetailWrap" v-if="detaileData">
            <div class="whiteBox">
                <div class="itemTop flexStart">
                    <van-image
                        class='classVideoImg'
                        fit="contain"
                        :src="detaileData.copyright.coverUrl"
                    />
                    <div class="classVideoTopMain">
                        <div class="videoName van-multi-ellipsis--l2">{{detaileData.copyright.title}}</div>
                        <div class="classLabel" :class="'Class1'" v-if="detaileData.copyright && detaileData.copyright.rating == 5">Class E</div>
                        <div class="classLabel" :class="'Class2'" v-else-if="detaileData.copyright && detaileData.copyright.rating == 4">Class D</div>
                        <div class="classLabel" :class="'Class3'" v-else-if="detaileData.copyright && detaileData.copyright.rating == 3">Class C</div>
                        <div class="classLabel" :class="'Class4'" v-else-if="detaileData.copyright && detaileData.copyright.rating == 2">Class B</div>
                        <div class="classLabel" :class="'Class5'" v-else-if="detaileData.copyright && detaileData.copyright.rating == 1">Class A</div>
                        <div class="videoScript van-multi-ellipsis--l2">{{detaileData.copyright.runtime}} | {{detaileData.copyright.genres.toString()}}</div>
                        <div class="videoTime">
                            <img class='videoTimeIcon' src="../assets/images/dateIcon.png" alt="">
                            <p class="timeText">{{detaileData.copyright.releaseDate}}</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="whiteBox" style="padding: 1rem .35rem 2.2rem;">
                <div class="statuLabel publishedStatus" v-if="detaileData.status == 'subscribed'">Subscribed</div>
                <div class="statuLabel pendingStatus" v-else-if="detaileData.status == 'redeemable'">Redeemable</div>
                <div class="statuLabel huiStatus" v-else-if="detaileData.status == 'redeemed'">Redeemed</div>
                <div class="statuLabel huiStatus" v-else-if="detaileData.status == 'cancelled'">Cancelled</div>
                <div class="statuLabel huiStatus" v-else-if="detaileData.status == 'revoked'">Revoked</div>
                <div class="subscriptionTitle">Subscription amount</div>
                <div class="amountNumber">{{moneySymbol+detaileData.amount}}</div>
                <div class="detaileBox flexBetween">
                    <div class="detaileLeft">
                        Started on:<br>
                        Cycle:<br>
                        Interest:<br>
                        Profit:<br>
                        Redemption amount:<br>
                        Redemption date:<br v-if="detaileData.status != 'subscribed' && detaileData.status != 'redeemable'">
                        <span v-if="detaileData.status == 'redeemed'">Redeemed on</span>
                        <span v-else-if="detaileData.status == 'cancelled'">Redeemed early on</span>
                        <span v-else-if="detaileData.status == 'revoked'">Revoked on</span>
                    </div>
                    <div class="detaileRight">
                        {{detaileData.submitTime}}<br>
                        {{detaileData.cycle}} days<br>
                        {{parseFloat((detaileData.interestRate*100).toFixed(2))}}%<br>
                        {{moneySymbol+ (detaileData.profit != 0 ? detaileData.profit : 'Not applicable')}}<br>
                        {{moneySymbol+detaileData.redemptionAmount}}<br>
                        {{detaileData.redeemAfter}}<br v-if="detaileData.status != 'subscribed' && detaileData.status != 'redeemable'">
                        <span v-if="detaileData.status == 'redeemed' || detaileData.status == 'cancelled' || detaileData.status == 'revoked'">{{detaileData.redeemTime}}</span>
                    </div>
                </div>
                <div class="buttonBox flexBetween">
                    <template v-if="detaileData.status == 'subscribed' && detaileData.copyright.subscriptionStatus == 'in_progress'">
                        <div class="submitBtn_91 blueAllowBtn" style="width: 3.32rem;" @click="goPage('/MovieDetails/'+detaileData.copyright.id)">Increase</div>
                        <div class="cancelBtn_91" style="width: 3.32rem;" @click="cancelAlertBox = true">Cancel</div>
                    </template> 
                    <template v-else-if="detaileData.status == 'subscribed' && detaileData.copyright.subscriptionStatus != 'in_progress'">
                        <div class="cancelBtn_91" style="width: 100%rem;" @click="cancelAlertBox = true">Cancel</div>
                    </template>
                    <template v-else-if="detaileData.status == 'redeemable'">
                        <div class="submitBtn_91 blueAllowBtn" style="width: 100%;" @click="cancelOrder(detaileData.id)">Redeem</div>
                    </template>
                    <template v-else>
                        <div class="submitBtn_91 blueAllowBtn" style="width: 100%;" @click="goDetail(detaileData.id)">Subscribe Again</div>
                    </template>
                </div>
                <div class="subscriptionAlert" v-if="detaileData.status == 'subscribed'">Early redemption will lose interest<br>and pay a {{parseFloat((detaileData.redeemFee*100).toFixed(2))}}% service fee.</div>
            </div>
        </div>
        <van-dialog class='alertModelBox' v-model="cancelAlertBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Caution
                    <img class='alertModelCloseIcon' @click="cancelAlertBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">The redemption date has not yet arrived.<br>Early redemption will lose interest and pay {{parseFloat((detaileData.redeemFee*100).toFixed(2))}}% service fee.</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 redBorderBtn" style="width: 2.9rem;" @click="cancelOrder(detaileData.id)">Continue</div>
                        <div class="submitBtn_91 redAllowBtn" style="width: 2.9rem;" @click="cancelAlertBox = false">Later</div>
                    </div>
                </div>
            </template>
        </van-dialog>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'SubscriptionDetail',
		data: function(){
			return {
				bgWrapHeight: 200,
                openStroe: true,
                moneySymbol: https.moneySymbol,
                detaileData: null,
                cancelAlertBox: false,
			}
		},
		created(){
			this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    id: this.$route.params.id
                }
				https.fetchGet('/api/library/subscription/item',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.detaileData = res.data
                        
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            cancelOrder: function(id){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    id: id
                }
				https.fetchPost('/api/library/subscription/cancel',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.$toast({
							message: 'Subscription cancelled',
							forbidClick: true,
							icon: require('../assets/images/successIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
                        this.cancelAlertBox = false
                        setTimeout(() => {
                            this.getData()
                        }, 2000);
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.cancelOrder()
					}
				})
			},
            goDetail() {
                if(this.detaileData.copyright.subscriptionStatus == 'in_progress') {
                    this.goPage('/MovieDetails/'+this.detaileData.copyright.id)
                }else {
                    this.goPage('/Libary')
                }
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
